.pricing{
    width: 100%;
    padding: 6rem 1rem;
    background: #000;
}

.cardContainer{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
}

.Card{
    border: 1px solid #eee;
    color: #eee;
    text-align: center;
    padding: 1rem;
}

.Card:hover{
    background-color: #141414;
}

.Card h3{
    font: 1.4rem;
    padding: 1rem;
}

.Card p{
    padding: 10px 0;
}

.bar{
    border-bottom: 1px solid #eee;
    width: 10%;
    margin: 1.5rem auto;
    display: block;
}

.btc{
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0;
}

.Card .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}

@media screen and (max-width: 740px) {
    .cardContainer{
        max-width: 90%;
        margin: auto;
        /* display: grid; */
        grid-template-columns: 1fr;
        grid-gap: 40px;
    }   
}